:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background-size: 100%;
  background-repeat: no-repeat;
  font-family: cursive;
  font-size: 20px;
  line-height: 1.5;
}

ul {
  padding: 0;
}

#searchCountry {
  background-color: rgb(238, 234, 234);
  margin: 40px 50px;
  padding: 15px;
  font-size: 20px;
  position: absolute;
  left: 530px;
}

.countryMarkup {
  top: 15%;
  left: 600px;
  position: absolute;
}
